@use '../../scss' as *;
.movie-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    grid-gap: 20px;
    gap: 20px;
    margin-bottom: 3rem;
    @include tablet {
        grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    }
    &__loadmore {
        text-align: center;
    }
}
.movie-search {
    max-width: 500px;
    position: relative;
    button {
        position: absolute;
        right: 0;
        top: 2px;
        @include mobile {
            right: 1px;
            top: 1px;
        }
    }
}
